import react, { useEffect , useState} from "react";
import CardComponents from "./components/CardComponents"
import './styles/stilo.scss'



function App() {
  const [character, setCharacter] = useState([]);
  const [value, setValue] = useState("");
  

  const intialUrl = "https://rickandmortyapi.com/api/character"

  const fetchCharater = (url) =>{
    fetch(url)
      .then (Response=>Response.json())
      .then(data => setCharacter(data.results))
      .catch(error => console.log(error))

  };

  useEffect(()=>{
    fetchCharater(intialUrl);
  },[])

  // Este es el filtro que uso para poder filtrar mis imagenes 
  const searchingChracter = (valor) => {
    return (character) => {
      return character.name.toLowerCase().includes(valor)||!valor;
    };
  };
  
  let characterMappeds = character
    .filter(searchingChracter(value))
    .map((personaje) => {
      return <CardComponents persona = {personaje}/>;
    });

  return (
    <>
    <div className = "top">
      <nav>
        <div className = "menu">
         <a className ="inicio" href = "/"><h1>Inicio</h1></a>
        </div>
      </nav>
    </div>
    <div className = "busqueda">
      <center>
        <input type = "texto" 
        placeholder = "buscar..." 
        onChange ={(e)=>setValue(e.target.value)} />
      </center>
    </div>
    <div className = "mapContent">{character && characterMappeds
    }</div>
    </>
  );
}

export default App;
