import React from 'react'
import "../styles/stilo.scss"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faAddressBook,faVenusMars,faVirus,faArrowAltCircleRight} from '@fortawesome/free-solid-svg-icons'
function CardComponents({persona}) {
    return (
        
        <div className = "Cardcomponents">
            <div className ="meta">
            <div><img className = "imgPersonaje" src = {persona.image} /></div>
            {/* <div className = "texto">
                Nombre: {persona.name}
            </div>
            <div className = "texto">
                Genero: {persona.gender}
            </div>
            <div className = "texto">
                Live of Death: {persona.status}
            </div> */}
            <ul className = "details">
                <li className ="author"><FontAwesomeIcon icon={faAddressBook} /> <a href="#">Nombre: {persona.name}</a></li>
                <li className ="date"><FontAwesomeIcon icon={faVenusMars} /> <a herf ="#">Genero: {persona.gender}</a></li>
                <li className ="tags">
                    <ul>
                    <li><FontAwesomeIcon icon={faVirus} /><a href="#">Especie: {persona.species}</a></li>
                    </ul>
                </li>
            </ul>
            </div>
            <div class="description">
                <h1>Biografia:  </h1>
                <h2>El desafio de UMA</h2>
                    <p> Esta es mi primera aplicacion pegandole a una api espero este bien si no me tirare del balcon posdata 
                        es mentira lo de tirarme :-P.</p>
                    <p class="read-more">
                    <a href="https://pelismart.com/serie-rick-y-morty-online/"target="_blank">Siguiente</a>
                    <FontAwesomeIcon icon={faArrowAltCircleRight} /> 
                    </p>
            </div>
        </div>
    )
}

export default CardComponents

